import Container from "@mui/material/Container";
import Grid from "@mui/material/Grid";
import MKBox from "components/MKBox";
import MKTypography from "components/MKTypography";
import TransparentBlogCard from "layouts/sections/TransparentBlogCard";
import BackgroundBlogCard from "layouts/sections/BackgroundBlogCard";
import post4 from "assets/images/examples/blog2.jpg";

function Places() {
  return (
    <MKBox component="section" py={2}>
      <Container>
        <Grid container item xs={12} lg={6}>
          <MKTypography variant="h3" mb={6}>
            Check my recent works
          </MKTypography>
        </Grid>
        <Grid container spacing={3}>
          <Grid item xs={12} sm={6} lg={3}>
            <TransparentBlogCard
              image="https://cdn.jsdelivr.net/gh/needleworm/aieng/src/assets/images/portfolios/bhban/books.jpg"
              title="Books"
              description=" 20    .  ?     5   ."
              action={{
                type: "external",
                route: "https://search.kyobobook.co.kr/search?keyword=%EB%B0%98%EB%B3%91%ED%98%84",
                color: "info",
                label: "Go!",
              }}
            />
          </Grid>
          <Grid item xs={12} sm={6} lg={3}>
            <TransparentBlogCard
              image="https://cdn.jsdelivr.net/gh/needleworm/aieng/src/assets/images/portfolios/bhban/youtube.jpg"
              title="YouTube"
              description="    ."
              action={{
                type: "external",
                route:
                  "https://www.youtube.com/channel/UCpV0ZdloVwvSjxHfnYYQPQg?sub_confirmation=1",
                color: "info",
                label: "Go!",
              }}
            />
          </Grid>
          <Grid item xs={12} sm={6} lg={3}>
            <TransparentBlogCard
              image="https://cdn.jsdelivr.net/gh/needleworm/aieng/src/assets/images/portfolios/bhban/researchgate.jpg"
              title="Researchgate"
              description="   10  30  !"
              action={{
                type: "external",
                route: "https://www.researchgate.net/profile/Byunghyun-Ban",
                color: "info",
                label: "Go!",
              }}
            />
          </Grid>
          <Grid item xs={12} sm={6} lg={3}>
            <BackgroundBlogCard
              image={post4}
              title="Official Website"
              description=", , ,         ?"
              action={{
                type: "external",
                route: "https://needleworm.github.io",
                label: "Go!",
              }}
            />
          </Grid>
        </Grid>
      </Container>
    </MKBox>
  );
}

export default Places;
